import React, {Component, Fragment } from 'react';
import Swiper from 'swiper';
import 'swiper/dist/css/swiper.min.css';
import './video-single-slider.scss';

export default class VideoSingleSlider extends Component {
    componentDidMount() {
        new Swiper('.gallery-top', {
            spaceBetween: 10,
            navigation: {
                nextEl: '.swiper-button-next',
                prevEl: '.swiper-button-prev',
            },
            thumbs: {
                swiper: new Swiper('.gallery-thumbs', {
                    spaceBetween: 10,
                    slidesPerView: 4,
                    freeMode: true,
                    watchSlidesVisibility: true,
                    watchSlidesProgress: true,
                })
            },
        });
    }

    renderSlides() {
        const { files: { screenshots } } = this.props.movie;

        return screenshots.map(({ url }) => (
            <div
                className="swiper-slide"
                style={{ backgroundImage: `url(${url})` }} />
        ));
    }

    render() {
        const slides = this.renderSlides();
        return (
            <Fragment>
                <div className="swiper-container gallery-top shadow">
                    <div className="swiper-wrapper">
                        {slides}
                    </div>
                    <button
                        type="button"
                        className="swiper-button swiper-button-next swiper-button-white" />
                    <button
                        type="button"
                        className="swiper-button swiper-button-prev swiper-button-white" />
                </div>
                <div className="swiper-container gallery-thumbs">
                    <div className="swiper-wrapper">
                        {slides}
                    </div>
                </div>
            </Fragment>
        );
    }
};