import React, { Component, Fragment } from 'react';
import { getMovie } from '../../services/video-portal-service';
import Spinner from '../spinner';
import VideoSingleCard from '../video-single-card';
import VideoSinglePlayer from '../video-single-player';
import ErrorAlert from '../error-alert';
import VideoSingleSlider from '../video-single-slider';

import './video-single.scss';

export default class VideoSingle extends Component {
    state = {
        movie: null,
        loading: true,
        error: false
    };

    onLoadedMovie = (movie) => {
        this.setState({ movie, loading: false });
    };

    onErrorLoading = () => {
        this.setState({ loading: false, error: true });
    };

    componentDidMount() {
        const id = this.props.movieID;
        getMovie(id)
            .then(this.onLoadedMovie)
            .catch(this.onErrorLoading);
    }

    renderSpinner() {
        return (
            <div className="d-flex justify-content-center">
                <Spinner />
            </div>
        );
    }

    renderContent() {
        const { movie } = this.state;

        return (
            <Fragment>
                <div className="container-fluid mb-5">
                    <VideoSinglePlayer movie={movie} />
                </div>
                <div className="container">
                    <VideoSingleCard movie={movie} />
                    
                    <div className="row justify-content-end py-3">
                        <div className="col-xl-8 pl-1">
                            <VideoSingleSlider movie={movie} />
                        </div>
                    </div>
                </div>
                <div
                    style={{ background: `linear-gradient(rgba(7, 54, 66, 0.7), rgba(7, 54, 66, 0.7)) ,url(${movie.files.posterUrl}) no-repeat center top / cover` }}
                    className="video-single--poster" />
            </Fragment>
        );
    }

    renserErrorAlert() {
        return (
            <div className="container">
                <div className="row justify-content-center">
                    <ErrorAlert />
                </div>
            </div>
        );
    }

    render() {
        const { movie, loading, error } = this.state;

        const spinner = loading ? this.renderSpinner() : null;
        const content = !!movie ? this.renderContent() : null;
        const errorAlert = error ? this.renserErrorAlert() : null;

        return (
            <section className="video-single py-5">
                {spinner}
                {content}
                {errorAlert}
            </section>
        );
    }
}