import React, { Component, Fragment } from 'react';
import './video-single-player.scss';
import Icons from '../icons';

export default class VideoSinglePlayer extends Component {
    state = {
        play: false
    };

    handlePlayMovie = () => {
        this.setState({ play: true });
    };

    renderPlayer() {
        const {
            params: { isHD, is3D, is4K },
            files: { videoSD, videoHD, video3D, video4K }
        } = this.props.movie;

        const url = isHD ? videoHD.url :
                    is3D ? video3D.url :
                    is4K ? video4K.url : videoSD.url;
        return (
            <video
                controls={true}
                src={url} />
        );
    }

    renderContent() {
        const {
            title,
            titleInEnglish
        } = this.props.movie;
        return (
            <Fragment>
                <h2 className="text-white-50 mb-0 h3">
                    {titleInEnglish}
                </h2>
                <h1 className="display-4 text-white mb-4">
                    {title}
                </h1>
                <button
                    onClick={this.handlePlayMovie}
                    className="btn btn-light py-2">
                    <Icons.IconPlay width="25" height="25" />
                </button>
            </Fragment>
        );
    }

    render() {
        const { play } = this.state;
        
        const player = play ? this.renderPlayer() :
                              this.renderContent();
        return (
            <div
                className="video-single-player--container d-flex flex-column justify-content-center align-items-center">
                {player}
            </div>
        );
    }
};
