import React from 'react';
import { Link } from 'react-router-dom'

const Header = () => {
    return (
        <nav className="navbar navbar-expand-lg navbar-dark bg-dark py-0 shadow">
            <Link to="/" className="navbar-brand">
                <strong>Видео Портал</strong>
            </Link>
        </nav>
    );
};

export default Header;