import React, { Component } from 'react';
import { getMovies } from '../../services/video-portal-service';
import VideoListCard from '../video-list-card';
import Spinner from '../spinner';
import ErrorAlert from '../error-alert';

export default class VideoList extends Component {
    state = {
        movies: null,
        loading: true,
        error: false
    };

    onLoadedMovies = (movies) => {
        this.setState({ movies, loading: false });
    };

    onError = (error) => {
        this.setState({ loading: false, error: true });
    };

    componentDidMount() {
        getMovies()
            .then(this.onLoadedMovies)
            .catch(this.onError);
    }

    renderSpinner() {
        return (
            <div className="mx-auto">
                <Spinner />
            </div>
        );
    }

    renderMovies(movie) {
        return (
            <div
                key={movie.id}
                className="col-xl-2 mb-5 px-2">
                <VideoListCard movie={movie} />
            </div>
        );
    }

    renderErrorAlert() {
        return (
            <div className="mx-auto">
                <ErrorAlert />
            </div>
        );
    }

    render() {
        const { movies, loading, error } = this.state;

        const spinner = loading ? this.renderSpinner() : null;
        const moviesList = !!movies ? movies.map((movie) => this.renderMovies(movie)) : null;
        const errorAlert = error ? this.renderErrorAlert() : null;

        return (
            <section className="py-5">
                <div className="container-fluid">
                    <div className="row">
                        {spinner}
                        {moviesList}
                        {errorAlert}
                    </div>
                </div>
            </section>
        );
    }
}