import React from 'react';
import './video-list-card.scss';
import Icons from '../icons';
import { Link } from 'react-router-dom';

const VideoCard = (props) => {
    const {
        id,
        title,
        year,
        countries,
        posterUrl,
        genres,
        params: {
            isHD,
            is3D,
            is4K,
            isNew
        }
    } = props.movie;

    const iconFormat = is3D ? <Icons.Icon3D width="40" height="40" /> :
                       isHD ? <Icons.IconHD width="40" height="40" /> :
                       is4K ? <Icons.Icon4K width="40" height="40" /> : null;
                       
    const badgeFormat = iconFormat ? (
        <span className="video-card-list--icon-format text-white">
            {iconFormat}
        </span>
    ) : null;


    const iconNew = isNew ? <Icons.IconNEW width="40" height="40" /> : null;
    const badgeNew = iconNew ? (
        <span className="video-card-list--icon-new text-danger">
            {iconNew}
        </span>
    ) : null;

    return (
        <Link to={`/movie/${id}`} className="video-card-list card bg-dark shadow rounded-0">
            <div className="video-card-list--poster">
                <img src={posterUrl} className="card-img-top" alt={title} />
                {badgeFormat}
                {badgeNew}
            </div>
            <div className="card-header bg-transparent">
                <h3 className="card-title h5 m-0 text-truncate text-white" title={title}>{title}</h3>
            </div>
            <ul className="list-group list-group-flush text-white-50">
                <li className="list-group-item text-truncate bg-transparent py-2" title={year}>
                    <strong>Год:</strong> {year}
                </li>
                <li className="list-group-item text-truncate bg-transparent py-2" title={countries}>
                    <strong>Страна:</strong> {countries}</li>
                <li className="list-group-item text-truncate bg-transparent py-2" title={genres}>
                    <strong>Жанры:</strong> {genres}
                </li>
            </ul>
        </Link>
    );
};

export default VideoCard;