import React from 'react';
import './video-single-card.scss';
import Icons from '../icons';

const castCrewRender = (castCrewList) => {
    return castCrewList.map(({ name }) => name).join(', ');
};

const VideoSingleCard = (props) => {
    const {
        year,
        title,
        titleInEnglish,
        countries,
        description,
        genres,
        cast,
        directors,
        producers,
        scenarists,
        ratings: {
            imdb,
            kinopoisk
        },
        params: {
            isHD,
            is3D,
            is4K,
            isNew
        },
        files: {
            posterUrl
        }
    } = props.movie;

    const castList = castCrewRender(cast);
    const directorsList = castCrewRender(directors);
    const scenaristsList = castCrewRender(scenarists);
    const producersList = castCrewRender(producers);

    const iconFormat = is3D ? <Icons.Icon3D width="40" height="40" /> :
                       isHD ? <Icons.IconHD width="40" height="40" /> :
                       is4K ? <Icons.Icon4K width="40" height="40" /> : null;

    const badgeFormat = !!iconFormat ? (
        <span className="video-single-card--icon-format text-white">
            {iconFormat}
        </span>
    ) : null;

    const iconNew = isNew ? <Icons.IconNEW width="50" height="50" /> : null;
    const badgeNew = !!iconNew ? (
        <span className="video-single-card--icon-new text-danger">
            {iconNew}
        </span>
    ) : null;


    return (
        <div className="video-single-card d-flex">
            <div className="col-4 p-0 d-flex align-items-center">
                <div className="video-single-card--poster shadow">
                    <img src={posterUrl} className="card-img-top" alt={title} />
                    {badgeNew}
                    {badgeFormat}
                </div>
            </div>
            <div className="card flex-grow-1 rounded-0 bg-dark text-white shadow">
                <div className="card-body">
                    <h2 className="card-title h5 text-muted">
                        {titleInEnglish}
                    </h2>
                    <h2 className="card-title">
                        {title}
                    </h2>
                    <p className="card-text">
                        {description.split(/&#039;/)}
                    </p>
                    <ul className="list-group list-group-flush text-muted">
                        <li className="list-group-item bg-transparent py-2">
                            <strong className="text-white-50">Год:</strong> {year}
                        </li>
                        <li className="list-group-item bg-transparent py-2">
                            <strong className="text-white-50">Страна:</strong> {countries}
                        </li>
                        <li className="list-group-item bg-transparent py-2">
                            <strong className="text-white-50">Жанр:</strong> {genres}
                        </li>
                        <li className="list-group-item bg-transparent py-2">
                            <strong className="text-white-50">В ролях:</strong> {castList}
                        </li>
                        <li className="list-group-item bg-transparent py-2">
                            <strong className="text-white-50">Режисеры:</strong> {directorsList}
                        </li>
                        <li className="list-group-item bg-transparent py-2">
                            <strong className="text-white-50">Сценаристы:</strong> {scenaristsList}
                        </li>
                        <li className="list-group-item bg-transparent py-2">
                            <strong className="text-white-50">Продюсеры:</strong> {producersList}
                        </li>
                    </ul>
                </div>
                <div className="card-footer">
                    <ul className="list-group list-group-horizontal text-muted">
                        <li className="bg-transparent py-1 px-2 d-flex align-items-center">
                            <strong className="text-white-50 mr-1">IMDb:</strong> {imdb}
                        </li>
                        <li className="bg-transparent py-1 px-2 d-flex align-items-center">
                            <strong className="text-white-50 mr-1">Кинопоиск:</strong> {kinopoisk}
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    );
};

export default VideoSingleCard;