const API_URL = 'http://cors-anywhere.herokuapp.com/https://api.itv.uz/api/content/main/2';
const API_KEY = '?user=53e4f6f20bd6e90b72b5dae562284534';

const transformVideoFileAPI = (videoFile) => {
    return {
        id: videoFile.file_id,
        url: videoFile.video_url,
        seconds: videoFile.seconds,
    };
};

const transformCastCrewListAPI = (castCrewList) => {
    return castCrewList.map(({ id, name, photo_url }) => {
        return {
            id,
            name,
            photoURL: photo_url
        };
    });
};

const transformMovieAPI = (movie) => {
    const {
        actors,
        directors,
        files,
        params,
        producers,
        scenarists
    } = movie;

    return {
        id: movie.id,
        year: movie.year,
        title: movie.title,
        titleInEnglish: movie.title_eng,
        countries: movie.countries_str,
        description: movie.description,
        ratings: movie.rates,
        genres: movie.genres_str,
        cast: transformCastCrewListAPI(actors),
        directors: transformCastCrewListAPI(directors),
        producers: transformCastCrewListAPI(producers),
        scenarists: transformCastCrewListAPI(scenarists),
        params: {
            isHD: params.is_hd,
            is3D: params.is_3d,
            is4K: params.is_4k,
            isNew: params.is_new,
            isFree: params.is_free,
            isTVShow: params.is_tvshow
        },
        files: {
            posterUrl: files.poster_url,
            trailers: files.trailers,
            videoSD: transformVideoFileAPI(files.video_sd),
            videoHD: transformVideoFileAPI(files.video_hd),
            video3D: transformVideoFileAPI(files.video_3d),
            video4K: transformVideoFileAPI(files.video_4k),
            screenshots: files.snapshots.map(({ snapshot_url }) => {
                return {
                    url: snapshot_url
                };
            })
        }
    };
};

const transformMovieListAPI = (movies = Array.prototype) => {
    return movies.map((movie) => {
        const { params } = movie;

        return {
            id: movie.id,
            title: movie.title,
            year: movie.year,
            countries: movie.countries_str,
            posterUrl: movie.files.poster_url,
            genres: movie.genres_str,
            params: {
                isHD: params.is_hd,
                is3D: params.is_3d,
                is4K: params.is_4k,
                isNew: params.is_new,
                isFree: params.is_free,
                isTVShow: params.is_tvshow
            }
        };
    });
};

const joinPath = (...path) => {
    return `${path.join('/')}`;
};

const getResourse = async (url = String.prototype) => {
    const response = await fetch(
        joinPath(API_URL, url, API_KEY)
    );
    if (!response.ok) throw new Error(`Не удалось получить данные по адресу: ${url}, получено ${response.status}`);
    return await response.json();
};

export const getMovies = async () => {
    const movies = await getResourse('list').then(({ data: { movies } }) => movies);
    return transformMovieListAPI(movies);
};

export const getMovie = async (id) => {
    const movie = await getResourse(`show/${id}`).then(({ data: { movie } }) => movie);
    return transformMovieAPI(movie);
};